.intro {
  background-color: $color-romance;
  padding-bottom: 54px;

  &:first-child {
    background-color: $color-jade;
  }

  &--businnes {
    background: $color-default-white;
  }

  .container {
    padding-top: 70px;
  }

  &__heading {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__description {
    max-width: 78%;
  }

  &__text {
    max-width: 43%;
  }

  &__title {
    margin-bottom: 32px;
  }

  &__list {
    padding-left: 60px;
    margin-top: -16px;
  }

  &__item {
    font-family: Montserrat;
    margin-top: 16px;
  }

  &__button-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 80px;
  }

  .subtitle {
    margin-bottom: 8px;
  }

  .text-25 {
    margin-bottom: 32px;
  }

  &__btn {
    padding: 24px 32px;
    border-radius: 50px;

    &--businnes {
      margin-top: 45px;
      padding: 15px 17px;
    }
  }
}
